import View from './View.js';

// import icons from '../img/sprite.svg'; // Parcel 1
import sprite from 'url:../../img/sprite.svg'; // Parcel 2
// import { Fraction } from 'fractional'; -- It happened error in fraction site
import fracty from 'fracty';

class RecipeView extends View {
  _parentElement = document.querySelector('.recipe');
  _errorMessage = 'We could not find that recipe. Please try another one!';
  _message = '';

  addHandlerRender(handler) {
    ['hashchange', 'load'].forEach(event =>
      window.addEventListener(event, handler)
    );
    // window.addEventListener('hashchange', handler);
    // window.addEventListener('load', handler);
  }

  addHandlerUpdateServings(handler) {
    this._parentElement.addEventListener('click', function (event) {
      const btn = event.target.closest('.btn__update-serving');
      if (!btn) return;
      const { updateTo } = btn.dataset;
      if (+updateTo > 0) handler(+updateTo);
    });
  }

  addHandlerAddBookmark(handler) {
    this._parentElement.addEventListener('click', function (event) {
      const btn = event.target.closest('.btn--bookmark');
      if (!btn) return;
      handler();
    });
  }

  _generateMarkup() {
    return `
     <div class="recipe__fig">
          <img
            src="${this._data.image}"
            alt="${this._data.title}"
            class="recipe__img"
          />
          <h1 class="recipe__title">
            <span>${this._data.title}</span>
          </h1>
        </div>

        <div class="recipe__details">
          <div class="recipe__info">
            <svg class="recipe__info-icon">
              <use href="${sprite}#icon-clock"></use>
            </svg>
            <div class="recipe__info-minutes recipe__info-data">${
              this._data.cookingTime
            }</div>
            <div class="recipe__info-text">minutes</div>
          </div>
          <div class="recipe__info">
            <svg class="recipe__info-icon">
              <use href="${sprite}#icon-users"></use>
            </svg>
            <div class="recipe__info-people recipe__info-data">${
              this._data.servings
            }</div>
            <div class="recipe__info-text">servings</div>

            <div class="recipe__info-buttons">
              <button data-update-to="${
                this._data.servings - 1
              }" class="btn__tiny btn__update-serving">
                <svg>
                  <use href="${sprite}#icon-circle-with-minus"></use>
                </svg>
              </button>
              <button data-update-to="${
                this._data.servings + 1
              }" class="btn__tiny btn__update-serving">
                <svg>
                  <use href="${sprite}#icon-circle-with-plus"></use>
                </svg>
              </button>
            </div>
          </div> 
              
          <div class="recipe__user-generated ${this._data.key ? '' : 'hidden'}">
            <svg>
              <use href="${sprite}#icon-users"></use>
            </svg>
          </div> 
            <button class="btn__round btn--bookmark ${
              this._data.bookmarked ? 'bookmarked' : ''
            }">
              <svg>
                 <use href="${sprite}#icon-bookmark"></use>
              </svg>
           </button>
        </div>

        <div class="recipe__ingredients">
          <h2 class="heading--2">Recipe ingredients</h2>
          <ul class="recipe__ingredient-list">
          ${this._data.ingredients.map(this._generateMarkupIngredient).join('')}
          </ul>
        </div>

        <div class="recipe__directions">
          <h2 class="heading--2">How to cook it</h2>
          <p class="recipe__directions-text">
            This recipe was carefully designed and tested by
            <span class="recipe__publisher">${
              this._data.publisher
            }</span>. Please
            check out directions at their website.
          </p>
          <a
            href="${this._data.sourceUrl}"
            target="_blank"
            class="btn__small recipe__btn"
          >
            <span>Directions</span>
            <svg class="search__icon">
              <use href="${sprite}#icon-arrow-long-right"></use>
            </svg>
          </a>
        </div> 
        `;
  }

  _generateMarkupIngredient(ing) {
    return `
      <li class="recipe__ingredient">
        <svg class="recipe__icon">
          <use href="${sprite}#icon-check"></use>
        </svg>
        <div class="recipe__quantity">${
          ing.quantity ? fracty(ing.quantity).toString() : ''
        }</div>
        <div class="recipe__description"> 
          <span class="recipe__unit">${ing.unit}</span> ${ing.description}
        </div>
      </li>
      `;
  }

  toggleBookmarkStyle(isBookmarked) {
    const bookmarkBtn = document.querySelector('.btn--bookmark');
    if (!bookmarkBtn) return;

    bookmarkBtn.classList.toggle('bookmarked', isBookmarked);
  }
}

export default new RecipeView();
